import { Injectable } from '@angular/core';
import { Team } from '../models/team';

@Injectable({
  providedIn: 'root',
})
export class ScoreDataService {
  constructor() {
    this.scores = [];
    this.roasters = [];
  }

  scores: Array<any>;
  roasters: Array<Team>;
  ourTeam: Team;

  setScores(data) {
    this.scores = data;
  }

  setRoasters(data) {
    this.roasters = data;
  }

  setOurTeam(team: Team) {
    this.ourTeam = team;
  }
}
