import { Component, ViewChild } from '@angular/core';
import { Team } from 'src/app/models/team';
import { TeamMember } from 'src/app/models/teamMember';
import { FlowService } from 'src/app/services/flow.service';
import { ScoreDataService } from 'src/app/services/score-data.service';
import { TeamPickComponent } from '../team-pick/team-pick.component';

@Component({
  selector: 'pick',
  templateUrl: './pick.component.html',
  styleUrls: ['./pick.component.css'],
})
export class PickComponent {
  ourTeam: Team;
  opponentTeam: Team;

  @ViewChild('ours') oursPickComponent: TeamPickComponent;
  @ViewChild('opponent') opponentPickComponent: TeamPickComponent;

  constructor(
    private flowService: FlowService,
    private scoreDataService: ScoreDataService
  ) {
    this.ourTeam = this.scoreDataService.ourTeam;
    this.opponentTeam = this.flowService.opponent;
  }

  calculateBestChanceRoasters(roaster: TeamMember) {
    debugger;
    if (this.opponentPickComponent.defender == null) return;

    var opponentTeamScores = this.scoreDataService.scores.filter(
      (x) => x.team == this.flowService.opponent.name
    )[0].scores;

    var opponentRoasterScores = opponentTeamScores.filter(
      (x) =>
        x.theirRoasterName == this.opponentPickComponent.defender.roasterName &&
        !this.flowService.pick.some((y) => y.roasterName == x.ourRoasterName) &&
        this.oursPickComponent.defender.roasterName != x.ourRoasterName
    );

    var maxScore = Math.max.apply(
      Math,
      opponentRoasterScores.map((x) => x.score)
    );

    var maxScoreRoasters = opponentRoasterScores.filter(
      (x) => x.score == maxScore
    );

    this.oursPickComponent.markBestChanceRoasters(maxScoreRoasters);
  }
}
